.fullscreen {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: -webkit-gradient(linear, left top, right bottom, from(#276caf), color-stop(#30a1c1), to(#9dd5ba));
  background: -webkit-linear-gradient(left top, #276caf, #30a1c1, #9dd5ba);
  background: -o-linear-gradient(left top, #276caf, #30a1c1, #9dd5ba);
  background: linear-gradient(to right bottom, #276caf, #30a1c1, #9dd5ba); }



