@-webkit-keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-webkit-keyframes fadeOut {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes fadeOut {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@-webkit-keyframes bounceUpIn {
  0% {
    -webkit-transform: translateY(250px);
            transform: translateY(250px);
    opacity: 0; }
  50% {
    opacity: 1;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px); }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px); } }

@keyframes bounceUpIn {
  0% {
    -webkit-transform: translateY(250px);
            transform: translateY(250px);
    opacity: 0; }
  50% {
    opacity: 1;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px); }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px); } }

@-webkit-keyframes bounceDownOut {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1; }
  50% {
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
    opacity: 1; }
  100% {
    -webkit-transform: translateY(250px);
            transform: translateY(250px);
    opacity: 0; } }

@keyframes bounceDownOut {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1; }
  50% {
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
    opacity: 1; }
  100% {
    -webkit-transform: translateY(250px);
            transform: translateY(250px);
    opacity: 0; } }

@-webkit-keyframes popFadeIn {
  0% {
    -webkit-transform: scale(0.9, 0.9);
            transform: scale(0.9, 0.9);
    opacity: 0; }
  100% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
    opacity: 1; } }

@keyframes popFadeIn {
  0% {
    -webkit-transform: scale(0.9, 0.9);
            transform: scale(0.9, 0.9);
    opacity: 0; }
  100% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
    opacity: 1; } }

@-webkit-keyframes popFadeOut {
  0% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
    opacity: 1; }
  100% {
    -webkit-transform: scale(0.9, 0.9);
            transform: scale(0.9, 0.9);
    opacity: 0; } }

@keyframes popFadeOut {
  0% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
    opacity: 1; }
  100% {
    -webkit-transform: scale(0.9, 0.9);
            transform: scale(0.9, 0.9);
    opacity: 0; } }

