  top: 0;
  left: 0;
  z-index: 5; }

.page .content.padding {
  padding-top: 3.5rem; }

.page .content.responsive {
  padding-left: 1rem;
  padding-right: 1rem;
  width: 1200px;
  margin: 0 auto;
  -webkit-transition: width .3s ease-in-out;
  -o-transition: width .3s ease-in-out;
  transition: width .3s ease-in-out; }
  @media (max-width: 1199px) {
